@keyframes animate{
    0%{
        transform: translate(-50%, -50%);
        opacity: 1;
        filter: hue-rotate(0);
    }
    100%{
        transform: translate(-50%, -6000%);
        opacity: 0;
        filter: hue-rotate(500deg);
    }
}
#body{
    height: 100vh;
    width: 100vw;
    overflow: hidden;
    background-image: linear-gradient(rgba(0,0,0,0.95),#000000f2), url('../../Images/Img3.jpg');
    background-position: center;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    /* background-color: black; */
    position: relative;
}
#body span{
    font-size: 150px;
    /* height: 100px;
    width: 100px; */
    color: red;
    position: absolute;
    pointer-events: none;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-shadow: 2px 2px 1px #fff, -2px -2px 1px #fff;
    animation: animate 6s linear;
    /* text-shadow: ; */
}

.heading{
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    font-size: 50px;
    /* z-index: 999; */
    color: #fff;
    text-align: center;
}


.footer{
    position: absolute;
    bottom: 0px;
    left: 0px;
    width: 100%;
    /* background-color: orange; */
    padding: 20px;
}
.g-link2{
    max-width: 150px;
    text-align: center;
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translate(-50%,-50%);
    padding: 10px 15px;
    background-color: transparent;
    border-radius: 5px;
    font-size: 18px;
    font-weight: 550;
    border: none;
    text-decoration: none;
    color: white;
    cursor: pointer;
}

@media only screen and (max-width:500px) {
    .g-link2{
        top: 0%;
    }
    .copy-right{
        font-size: 50px;
        display: none;
    }
}

.g-link2:hover{
    background-color: orange;
}

.copy-right{
    display: block;
    color: #fff;
    font-size: 12px;
    position: absolute;
    bottom: 10px;
    left: 10px;
}