* {
  margin: 0;
  padding: 0;
}
body {
    /* font-family: 'Roboto', sans-serif; */
    font-family: 'Poppins';
  }
  
  
.main {
    position: relative;
  height: 100vh;
  width: 100vw;
  /* background-color: rgb(255, 228, 174); */
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url("../../Images/Img1.jpg");
  background-position: center;
  background-size: cover;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.title{
    position: absolute;
    left: 50%;
    top: 50px;
    transform: translateX(-50%);
    color: #fff;
    font-size: 30px;
    font-weight: 550;
}

.sh{
    color: rgb(255, 115, 0);
    font-size: 35px;
    text-transform: uppercase;
}

.container {
  margin-left: 100px;
  max-width: 250px;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.623);
  padding: 50px;
  border: 1px solid black;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
@media only screen and (max-width: 500px) {
  /* .main {
    justify-content: center;
  } */
  .container {
    margin-left: 0px;
  }
}

.btn {
  color: #fff;
  font-size: 18px;
  margin: 30px 0px;
  background-color: transparent;
  padding: 10px 0px;
  outline: none;
  border: none;
  border-bottom: 1px solid orange;
  font-family: Arial, Helvetica, sans-serif;
}

.g-link {
  margin: 10px;
  width: 100%;
  padding: 10px 15px;
  background-color: orange;
  border-radius: 5px;
  font-size: 20px;
  font-weight: 550;
  border: none;
  color: white;
  cursor: pointer;
}

.link-section {
  width: 250px;
  overflow-x: hidden;
}

.link {
  overflow-x: scroll;
  margin: 10px;
  font-size: 20px;
  font-weight: 500;
  color: White;
  text-decoration: none;
  text-align: center;
}

.invisible {
  min-width: 250px;
  max-width: 250px;
  display: none;
  text-align: center;
}

.fname,
.sname {
  font-size: 15px;
  color: #fff;
  margin-bottom: 10px;
  display: none;
}

.copy-right {
  color: #fff;
  font-size: 12px;
  position: absolute;
  bottom: 10px;
  left: 10px;
}
